.close-oops {
  width: 15px;
  height: 15px;
  stroke: #fff;
  transition-property: fill;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  display: block;
}

.backdrop-oops {
  width: 100%;
  height: 100%;
  z-index: 1000;
  background-color: rgba(46, 47, 66, .7);
  position: fixed;
  top: 0;
  left: 0;
}

.is-hidden {
  pointer-events: none;
  opacity: 0;
  display: none;
}

.modal-oops {
  width: 280px;
  height: 289px;
  background-color: #000;
  border-radius: 16px;
  padding: 39px 33px 8px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 50%;
  overflow-y: scroll;
  transform: translate(-50%, -50%)scale(1);
  box-shadow: 1px 1px 14px 4px rgba(255, 107, 8, .42);
}

.modal-oops::-webkit-scrollbar {
  display: none;
}

.close-modal-oops {
  width: 24px;
  height: 24px;
  color: var(--white-smoke);
  background-color: #000;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  padding: 0;
  transition-property: background-color, border;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  top: 24px;
  right: 24px;
}

.close-modal-oops:hover {
  color: #f87719;
}

