.switch {
  width: 3.5em;
  height: 2em;
  font-size: 17px;
  display: inline-block;
  position: relative;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  height: 32px;
  cursor: pointer;
  filter: drop-shadow(0 0 6px #6b6969);
  background: linear-gradient(#000, #003);
  border-radius: 30px;
  justify-content: space-between;
  align-items: center;
  padding: 6px;
  transition: all .4s;
  display: flex;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.slider:before {
  content: "";
  height: 1.4em;
  width: 1.4em;
  background: linear-gradient(74deg, #000, #003, #888);
  border-radius: 20px;
  transition: all .4s;
  animation: anima1 .3s linear;
  position: absolute;
  bottom: .3em;
  left: .3em;
}

@keyframes anima1 {
  0% {
    transform: translateX(1.5em);
  }

  80% {
    transform: translateX(-.3em);
  }

  100% {
    transform: translateX(0);
  }
}

.switch input:checked + .slider:before {
  background: linear-gradient(90deg, #f47622, #fff);
  animation: anima .3s linear;
  transform: translateX(1.5em);
  box-shadow: 0 0 10px rgba(0, 0, 0, .7);
}

.switch input:checked + .slider {
  background: linear-gradient(232deg, #ffc226 9.4%, #f84119 91.91%);
}

@keyframes anima {
  0% {
    transform: translateX(0);
  }

  80% {
    transform: translateX(1.6em);
  }

  100% {
    transform: translateX(1.4em);
  }
}

